import Vue from "vue";

export const state = Vue.observable({
  activeFeatures: [],
  routesCache: {},
  routeColor: {
    cycling: "#AAE08F",
    car: "#67000D",
    walking: "#2E62DB",
    rail: "#EC7158",
    pt: "#EC7158"
  },
  ptDetailIcons: {
    WALKING: { icon: "directions_walk", category: "walking" },
    HEAVY_RAIL: { icon: "train", category: "rail" },
    COMMUTER_RAIL: { icon: "train", category: "rail" },
    COMMUTER_TRAIN: { icon: "train", category: "rail" },
    HIGH_SPEED_TRAIN: { icon: "train", category: "rail" },
    TRAM: { icon: "tram", category: "rail" },
    BUS: { icon: "directions_bus", category: "rail" }
  },
  panel: [0]
});

export const mutations = {
  addActiveFeature(feature) {
    const start = feature[0].get("start");
    const mode = feature[0].get("mode");
    const gate = feature[0].get("gate");
    let keyName = `${start}__${mode}`;
    if (mode === "car") {
      keyName += `__${feature[0].get("traffic_time")}`;
    }
    const index = state.activeFeatures.findIndex(x => x.name === keyName);
    if (index !== -1) {
      //Using Vue.set due to reactivity problems in Vue.js version 2
      Vue.set(state.activeFeatures[index].data, gate, feature);
    } else {
      state.activeFeatures.push({
        name: keyName,
        mode: mode,
        data: {
          [gate]: feature
        }
      });
    }
  },
  clearActiveFeatures() {
    state.activeFeatures = [];
  },
  addRoutesInCache(name, olFeatures) {
    state.routesCache[name] = olFeatures;
  },
  panelChanged(value) {
    state.panel = value;
  }
};
